.add-task-div {
  padding: 15px;
  opacity: 1;
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 0;
  background-color:  #161d26;
  z-index: 3;
  width: calc(100% - 46px);
  border: .5px solid rgba(174, 174, 174, .25);
  transition: opacity .25s linear;
}

.add-task-div.hidden {
  z-index: -1;
  opacity: 0;
}

.add-task-item {
  padding: 7.5px;
  margin: 7.5px;
  width: calc(100% - 32px);
  min-height: calc((var(--item-line-height) * 3 ));
  border: 1px solid rgba(255, 255, 255, .1);
  opacity: .75;
  font-weight: normal;
  border-radius: 5px;
}

.add-task-item-title {
  width: 100%;
  height: 25px;
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 2.5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.add-task-input {
  font-size: 1em;
  margin-bottom: 5px;
  background-color: rgba(23, 29, 38, 1);
  border: .5px solid rgba(174, 174, 174, .75);
  color: #fff;
  padding: 7.5px;
  min-width: calc(100% - 15px);
  max-width: calc(100% - 15px);
}

.add-task-textarea {
  min-width: calc(100% - 15px);
  max-width: calc(100% - 15px);
  background-color: rgba(23, 29, 38, 1);
  border: .5px solid rgba(174, 174, 174, .75);
  padding: 7.5px;
  color: #fff;
  min-height: 75px;
  max-height: 175px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 16px;
}

.add-task-btns {
  width: 100%;
  height: 30px;
  opacity: 1;
  display: flex;
  justify-content: space-between;
  margin: 15px 0;
  transition: all .25s linear;
}

@media screen and (max-width: 767px){ 
  .add-task-div {
    width: calc(100% - 30px);
    border: 1.5px solid rgba(174, 174, 174, .25);
  }
}