.main-modal-window {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  margin-right: 0;
  display: flex;
  transition: all .25s linear;
}

.main-modal-window.hidden {
  margin-right: -100%;
}

.window-left {
  width: 50%;
  height: 100%;
}

.window-right {
  width: calc(50% - .5px);
  height: 100%;
  background-color: rgba(22, 29, 38, .85);
  border-left: .5px solid rgba(174, 174, 174, .25);
}

@media screen and (max-width: 767px){
  .window-left {
    width: 0%;
    height: 100%;
  }

  .window-right {
    width: calc(100% - 3px);
    height: calc(100% - 3px);
    border: 1.5px solid rgba(174, 174, 174, .1);
  }
}