.sort-type.search {
  width: calc(((100% / 4) * 2) - .5px);
}

.search-input {
  width: calc(92.5% - 10px);
  height: 50%;
  font-weight: bold;
  cursor: pointer;
  background-color: rgba(23, 29, 38, 1);
  border: .5px solid rgba(174, 174, 174, .75);
  color: #fff;
  padding: 2.5px 7.5px;
}

@media screen and (max-width: 767px) { 
  .sort-type.search {
    width: calc(((100% / 4) * 2) - 2.5px);
  }

  .sort-title-sp {
    font-size: .6em;
  }
}