.filter-div {
  border-bottom: .5px solid rgba(174, 174, 174, .25);
  opacity: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, .05);
}

.filter-div.hidden {
  height: 0;
  border-bottom: none;
  opacity: 0;
  z-index: -1;
}

.filter-div.hidden div, .filter-div.hidden span {
  display: none;
}

.filter-div.hidden span {
  display: none;
}

.filter-sort {
  width: 100%;
  height: 29.5px;
  display: flex;
  border-bottom: .5px solid rgba(174, 174, 174, .25);
  pointer-events: all;
  opacity: 1;
}

.filter-sort:last-child {
  height: 30px;
  border-bottom: none;
}

.sort-title {
  width: calc(100% / 4 - 15px);
  height: 100%;
  font-size: .7em;
  padding-left: 15px;
  font-weight: bold;
  /* font-style: italic; */
  border-right: .5px solid rgba(174, 174, 174, .25);
  display: flex;
  align-items: center;
}

.sort-type {
  width: calc(100% / 4 - .5px);
  height: 100%;
  font-size: .8em;
  font-weight: bold;
  /* font-style: italic; */
  border-right: .5px solid rgba(174, 174, 174, .25);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  pointer-events: all;
  opacity: 1;
}

.sort-type.disabled, .sort-title.disabled {
  cursor: none;
  pointer-events: none;
  opacity: .5;
}

.item-type {
  width: calc(100% / 3 - .5px);
  font-size: .9em;
  font-weight: bold;
  border-right: .5px solid rgba(174, 174, 174, .25);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.sort-type:last-child {
  width: calc(100% / 4);
  border-right: none;
  
}

.sort-type:hover, .item-type:hover {
  color: #161d26;
  background-color: lightgreen;
}

.sort-title.toggled, .sort-type.toggled, .item-type.toggled {
  color: #161d26;
  background-color: rgba(255, 255, 255, 1);
}

.sort-click-icon {
  margin-bottom: -2.5px;
  margin-right: 5px;
}