.bottom-center {
  width: 100%;
  overflow-y: auto;
  /* scrollbar-color: rgba(174, 174, 174, .5) rgba(174, 174, 174, .05); */
  /* scrollbar-width: thin; */
  padding-top: 7.5px;
  max-height: calc(82.5% - 7.5px);
  opacity: 1;
  pointer-events: all;
  transition: opacity .25s linear;
}

.bottom-center.add-task-item-toggled {
  opacity: .05;
  pointer-events: none;
}

@media screen and (max-width: 767px){
  .center-sp {
    z-index: 1;
    left: 0;
    transition: all .25s linear;
  }
}