/* common style */
.App {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  background-color: #161d26;
  position: relative;
  color: #fff;
  letter-spacing: .25px;
  height: 100%;
  overflow: hidden;
}

input::placeholder, textarea::placeholder {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 16px;
}

.item-length {
  background-color: #fff;
  color: #161d26;
  font-size: .75em;
  font-weight: bold;
  padding: .5px 5px;
  border-radius: 50px;
  text-align: center;
}

section {
  width: calc(100% / 3 - 1px);
  height: 100%;
  position: relative;
  opacity: 1;
  pointer-events: all;
  transition: opacity .25s linear;
  background-color: rgba(22,29,38, 1);
  border-left: 1px solid rgba(174, 174, 174, .1);
  background-image: url("../images/bg-logo1p.png");
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
}

section:first-child {
  width: calc(100% / 3 - 1px);
  border-left: none;
}

section.add-task-item-toggled, div.add-task-item-toggled {
  opacity: .5;
  pointer-events: none;
}

.title-div {
  width: calc(100% - 30px);
  min-height: calc(10% - 2px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  border-top: 1px solid rgba(174, 174, 174, .1);
  border-bottom: 1px solid rgba(174, 174, 174, .1);
  font-weight: 500;
}

.title-div-title {
  width: 70%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.title-text-disabled {
  opacity: .5;
  pointer-events: none;
}

.title-unnoted-label-style {
  text-decoration-line: line-through;
  text-decoration-color: #fff;
  text-decoration-thickness: 0.2rem;
}

.title-div-btns {
  width: 30%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.contents-div {
  width: 100%;
  max-height: 90%;
  overflow-y: auto;
  scrollbar-color: rgba(174, 174, 174, .5) rgba(174, 174, 174, .05);
  scrollbar-width: thin;
}

.touchable-item-title {
  width: 100%;
  height: 25px;
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 2.5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.touchable-item-edit-flag {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  background-color: #161d26;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.touchable-item-edit-flag.toggled {
  background-color: #fff;
  color: #161d26;
}

.touchable-item-edit-flag-inside {
  width: 80%;
  height: 80%;
}

.notes-completed {
  border: none;
  border-bottom: .5px solid rgba(174, 174, 174, .75);
  height: 50px;
  white-space: pre-wrap; 
}

@media screen and (max-width: 767px) {
  section, section:first-child, section:last-child {
    width: calc(100% - 2px);
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-right: 1px solid rgba(174, 174, 174, .1);
    border-left: 1px solid rgba(174, 174, 174, .1);
  }
  
  .title-div {
    width: calc(100% - 30px);
    min-height: 6%;
    border: none;
  }

  .contents-div {
    width: 100%;
    height: calc(94% - 100px);
    border-top: none;
    padding-bottom: calc(7.5% + 100px)
  }

  .initial-items-sp {
    min-height: 300px;
    max-height: 300px;
  }
}