.header {
  width: calc(100% - 30px);
  height: 7.5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  
}

.header-logo {
  height: 100%;
  width: 20%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.header-side {
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
}

.header-center {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-side.left {
  justify-content: flex-start;
}

.header-side.right {
  justify-content: flex-end;
}

.header-list-name {
  max-width: 80%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-all;
  font-weight: 500;
}

.header-list-length {
  width: 10%;
}