:root {
  --alert-height: 7.5%;
}

/* back-div */
.back-div {
  z-index: 4;
  opacity: 1;
  pointer-events: auto;
  position: fixed;
  transition: opacity .25s linear;
  top: 0;
  left: calc(50% - 375px);
}

.back-div.all {
  width: 100%;
  height: 100%;
  background-color: rgba(22, 29, 38, .5);
  left: 0;
}

.back-div.top {
  width: 750px;
  height: var(--alert-height);
  margin: 15px auto;
}

.back-div.hidden {
  z-index: -1;
  opacity: 0;
  pointer-events: none;
}

/* front-div */
.front-div {
  z-index: 5;
  background-color: rgba(22, 29, 38, 1);
}

.front-div.all {
  width: 100%;
  height: 100%;
  margin-top: 0;
  transition: all .25s linear;
  /* border: .5px solid rgba(255, 255, 255, .5); */
}

.front-div.top {
  width: 750px;
  height: var(--alert-height);
  margin: 15px auto;
  /* border: .5px solid rgba(255, 255, 255, .25); */
}

/* alert background color div */
.alert-bg-color {
  width: 100%;
  height: 100%;
  display: flex;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .25);
  color: #fff;
}

.alert-bg-color.blue {
  
}

.alert-bg-color.green {
}

.alert-bg-color.red {
  background-color: rgba(125, 0, 0, .5);
  color: #fff;
}

.alert-bg-color.white {
}

/* alert structure div */
.alert-structure {
  padding: 15px;
  width: calc(100% - 30px);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* alert text div */
.alert-text-div {
  width: 75%;
  display: flex;
  flex-direction: flex-start;
  align-items: center;
}

.alert-icon {
  width: 0;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.alert-text {
  min-width: 100%;
  padding-right: 2px;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: .75em;
  font-weight: 480;
  text-align: center;
}

.alert-text-inside {
  text-align: left;
}

/* alert btns div */
.alert-btns-div {
  width: 25%;
  display: flex;
  justify-content: space-between;
}

.close-btn-sp {
  display: none;
}

.close-btn-pc {
  display: block;
}

@media screen and (max-width: 767px) {
  .back-div {
    left: calc(50% - 175px);
  }

  .back-div.top {
    width: 350px;
    height: auto;
  }

  .front-div.top {
    width: 349px;
    height: auto;
    
  }

  .alert-structure {
    flex-direction: column;
    position: relative;
  }

  .alert-text-div {
    width: 100%;
  }

  .alert-text-inside {
    text-align: left;
    width: 95%;
  }

  .alert-btns-div {
    width: 100%;
    justify-content: space-between;
  }

  .alert-btns-div-sp {
    margin-top: 15px;
  }

  .close-btn-sp {
    display: block;
    width: 15px;
    height: 15px;
    position: absolute;
    top: calc(50% - 7.5px);
    right: 15px;
  }

  .close-btn-pc {
    display: none;
  }
}