button {
  width: 75px;
  height: 30px;
  border: none;
  background-color: #161d26;
  color: #fff;
  border: 1px solid #fff;
  padding: 5px 15px;
  border-radius: 5px;
  opacity: 1;
  pointer-events: all;
}

button.icon-btn {
  width: 25px;
  height: 25px;
  color: #fff;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  padding: 0;
  font-size: 20px;
  margin: 0;
}

button:hover, .icon-btn.toggled {
  background-color: #fff;
  color: #161d26;
}

button.disabled {
  opacity: .25;
  pointer-events: none;
}

button.icon-btn:not(:last-child ){
  margin-right: 5px;
}

.alert-btn {
  background-color: transparent;
  /* margin-left: 15px; */
}

/* toggle switch */
.switch-btn {
  display: flex;
  align-items: center;
  width: 100px;
  height: 15px;
  justify-content: flex-end;
}

.switch-div {
  position: relative;
}

input[type='checkbox'] {
  position: absolute;
  width: 0;
  height: 0;
}

.switch-btn-circle {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 12px;
  height: 12px;
  border-radius: 12px;
  background-color: #fff;
  transition: 0.25s;
}

input:checked ~ .switch-btn-circle {
  background-color: rgba(0, 100, 0, 1);
  transform: translateX(100%);
  transition: 0.25s;
}

.switch-btn-slider {
  width: 28px;
  border-radius: 15px;
  height: 16px;
  background-color: rgba(174, 174, 174, .25);
}

input:checked ~ .switch-btn-slider {
  background-color: rgba(0, 255, 0, 1);
}

.switch-btn-icon {
  display: flex;
  align-items: center;
  margin-left: 5px;
}

.switch-icon {
  color: "blue";
}

@media screen and (max-width: 767px) and (pointer: fine){
  button:active {
    background-color: #fff;
    color: #161d26;
  }

  button:hover {
    background-color: #161d26;
    color: #fff;
    pointer-events: none;
  }

  button:active {
    background-color: #fff;
    color: #161d26;
  }

  .alert-btns-div {
    position: relative;
  }

  .alert-btn {
    margin-left: 0;
    margin-top: 15px;
  }

  .delete-btn-sp {
    margin-left: 15px;
  }
}