.menu-sp-div {
  display: none;
}

@media screen and (max-width: 767px) {
 .menu-sp-div {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 3;
    width: 100%;
    height: 8.5%;
    padding-top: 5px;
    background-color: #161d26;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    border-top: 1px solid rgba(174, 174, 174, .1);
  } 

  button.menu-sp-btn {
    width: 50px;
    height: 50px;
    padding: 5px;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    pointer-events: all;
  }

  .menu-sp-btn:hover {
    background-color: #161d26;
    color: #fff;
  }

  .menu-sp-btn:active {
    background-color: #fff;
    color: #161d26;
  }

   .menu-sp-btn.disabled {
    opacity: .5;
    pointer-events: none;
   }

  .menu-sp-btn-icon {
    width: 100%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .menu-sp-btn-text {
    width: 100%;
    height: 20%;
    font-size: .7em;
    text-align: center;
  }
}