:root {
  --task-filter-div-height: 180px;
  --task-search-div-height: 90px;
}

/* task filter */
.filter-div.task-filter {
  height: var(--task-filter-div-height);
}

/* task search */
.filter-div.task-search {
  height: var(--task-search-div-height);
}

/* contents when task-filter is toggled */
.contents-div.filter-height.task-filter {
  max-height: calc(90% - 8.5px - var(--task-filter-div-height));
}

/* contents when task-search is toggled */
.contents-div.filter-height.task-search {
  max-height: calc(90% - .5px - var(--task-search-div-height));
}

/* contents when task-filter and task-search is toggled */
.contents-div.filter-height.task-filter-and-search {
  max-height: calc(90% - 9px - var(--task-filter-div-height) - var(--task-search-div-height));
}