.toggle-description-sp {
  display: none;
}

.content-div-description {
  width: calc(100% - 30.5px);
  height: calc(90% - 30px);
  padding: 15px;
}

@media screen and (max-width: 767px) {
  .right-sp {
    z-index: 2;
    top: 0%;
    transition: all .25s linear;
  }

  .right-sp.hidden {
    top: 100%;
    transition: all .25s linear;
  }

  .toggle-description-sp {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}