:root {
  --list-filter-div-height: 150px;
  --list-search-div-height: 120px;
}

/* list filter */
.filter-div.list-filter {
  height: var(--list-filter-div-height);
}

/* list search */
.filter-div.list-search {
  height: var(--list-search-div-height);
}

/* contents when list-filter is toggled */
.contents-div.filter-height.list-filter {
  max-height: calc(90% - 8.5px - var(--list-filter-div-height));
}

/* contents when list-search is toggled */
.contents-div.filter-height.list-search {
  max-height: calc(90% - .5px - var(--list-search-div-height));
}

/* contents when list-filter and list-search is toggled */
.contents-div.filter-height.list-filter-and-search {
  max-height: calc(90% - 9px - var(--list-filter-div-height) - var(--list-search-div-height));
}

.range-input {
  width: 40%;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  background-color: rgba(23, 29, 38, 1);
  border: .5px solid rgba(174, 174, 174, .75);
  color: #fff;
  padding: 2.5px;

}