.comp-range-div {
  height: 15px;
  display: flex;
  align-items: center;
  width: 100px;
}

.comp-range-item-div {
  width: 70%;
  height: 100%;
  height: 15px;
  margin-right: 0;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(174, 174, 174, .25);
  transition: all .25s linear;
}

.comp-range-div > span {
  width: 40px;
  font-size: .8em;
  margin-right: 5px;
  transition: all .25s linear;
  text-align: right;
  font-weight: bold;
}
  
.comp-range {
  width: 28px;
  height: 100%;
  background-color: rgba(174, 174, 174, .25);
  margin-right: 5px;
}

.completed {
  width: 45%;
  height: 100%;
  background-color: rgba(0, 255, 0, 1);
  transition: all .25s linear;
}

.comp-item-completed {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.comp-item-text {
  font-size: .5em;
  color: #fff;
  z-index: 3;
}


.comp-range-item-div2 {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.comp-range-item-top2 {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: .8em;
  font-weight: 480;
  font-weight: bold;
}

.comp-range-item-bottom2 {
  width: 100%;
  height: 20%;
  background-color: rgba(174, 174, 174, .25);
  display: flex;
  align-items: center;
  transition: all .25s linear;
}

.comp-range-item-bottom-completed2 {
  height: 100%;
  background-color: rgba(0, 255, 0, 1);
  transition: all .25s linear;
}