.add-list-div {
  height: 100%;
  overflow-y: auto;
  scrollbar-color: rgba(174, 174, 174, .5) rgba(174, 174, 174, .05);
  scrollbar-width: thin;
}

.add-list-items {
  width: calc(100% - 30px);
  height: calc(90% - 15.5px);
  padding: 7.5px 15px;
  display: flex;
  flex-direction: column;
}

.add-list-item-title {
  width: 100%;
  height: 25px;
  font-size: 1em;
  margin-bottom: 2.5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.add-list-item-title {
  width: 100%;
  height: 25px;
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 2.5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.add-list-input {
  font-size: 1em;
  margin-bottom: 5px;
  background-color: rgba(23, 29, 38, 1);
  border: .5px solid rgba(174, 174, 174, .75);
  color: #fff;
  padding: 7.5px;
  min-width: calc(100% - 15px);
  max-width: calc(100% - 15px);
}

.add-list-input.confirm {
  border: .5px solid rgba(174, 174, 174, .15);
  border-bottom: .5px solid rgba(174, 174, 174, .75);
}

.add-list-textarea {
  min-width: calc(100% - 15px);
  max-width: calc(100% - 15px);
  background-color: rgba(23, 29, 38, 1);
  border: .5px solid rgba(174, 174, 174, .75);
  padding: 7.5px;
  color: #fff;
  min-height: 75px;
  max-height: 175px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 16px;
}

.add-list-textarea.items {
  min-height: 150px
}

.textarea-confirm {
  border: .5px solid rgba(174, 174, 174, .15);
  border-bottom: .5px solid rgba(174, 174, 174, .75);
  padding: 7.5px;
  height: auto;
  white-space: pre-wrap;
}

.textarea-confirm-ul {
  margin: 0;
  padding: 7.5px;
  /* border: .5px solid rgba(174, 174, 174, .15); */
  /* border-bottom: .5px solid rgba(174, 174, 174, .75); */
}

.textarea-confirm-ul li {
  list-style-type: circle;
  list-style-position: inside;
}

.li::marker {
  margin: 0;
}

.add-list-btns {
  width: 100%;
  opacity: 1;
  display: flex;
  justify-content: space-between;
  margin: 15px 0;
  padding-bottom: 30px;
  transition: all .25s linear;
}