.description-div {
  width: calc(100% - 31px);
  min-height: calc(90% - 30px);
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.description-item {
  width: 100%;
  display: flex;
  flex-direction: column;
  font-weight: 480;
  transition: all .25s linear;
  margin-bottom: 15px;
}

.description-item-span {
  font-size: .75em;
}

.description-item-span-completed {
  font-size: .75em;
  color: rgb(0, 255, 0);
}

.description-item-label {
  width: 100%;
  font-size: 1em;
  margin-bottom: 2.5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text-editing {
  background-color: #fff;
  border-radius:  50%;
  color: #161d26;
  padding: 1px
}

.description-input {
  font-size: 0.85em;
  background-color: transparent;
  border: .5px solid rgba(174, 174, 174, .75);
  color: #fff;
  padding: 7.5px;
  min-width: calc(100% - 16px);
  max-width: calc(100% - 16px);
}

.description-input.read-only, .description-textarea.read-only {
  border-top: none;
  border-left: none;
  border-right: none;
  font-weight: 480;
  border-radius: 2.5px;
  pointer-events: none;
}

.description-textarea.read-only {
  height: 150px;
}

.description-textarea {
  min-width: calc(100% - 16px);
  max-width: calc(100% - 16px);
  background-color: transparent;
  border: .5px solid rgba(174, 174, 174, .75);
  padding: 7.5px;
  color: #fff;
  height: 150px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: 0.85em;
}

.description-items-btns {
  width: 100%;
  height: 30px;
  opacity: 1;
  display: flex;
  justify-content: space-between;
  margin-top: 7.5px;
  transition: all .25s linear;
}

.description-items-btns.disabled {
  text-decoration: line-through;
}

.description-sp {
  display: none;
}

.description-link {
  color: #fff;
  margin-bottom: 2.5px;
}

.notes-completed {
  border: none;
  border-bottom: .5px solid rgba(174, 174, 174, .75);
  height: auto;
  white-space: pre-wrap; 
}

@media screen and (max-width: 767px) {
  .description-sp {
    display: block;
  }
}