.empty-msg-div {
  width: 100%;
  height: calc(90% - 90px);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 90px;
}

.msg-div {
  width: 50%;
  height: 20%;
  border-radius: 15px;
  border: 2.5px solid rgba(174, 174, 174, .25);
  color: rgba(174, 174, 174, .75);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.msg-div-item-title {
  font-weight: 480;
  font-style: italic;
  font-size: .8em;
}