:root {
  --item-line-height: 21px;
}

.item-div {
  padding: 7.5px;
  margin: 7.5px;
  width: calc(100% - 32px);
  min-height: calc((var(--item-line-height) * 3 ));
  border: 1px solid rgba(255, 255, 255, .1);
  opacity: .75;
  font-weight: normal;
  border-radius: 5px;
  z-index: 5;
}

.item-div:hover, .item-selected {
  opacity: 1;
  font-weight: 480;
}

.item-div.item-selected {
  width: calc(100% - 32px);
  min-height: calc((var(--item-line-height) * 3 ) - 1px);
}

.item-div.item-selected {
  background-color: rgba(255, 255, 255, .1); 
  border: 1px solid rgba(255, 255, 255, .1);
  width: calc(100% - 32px);
  min-height: calc((var(--item-line-height) * 3 ) - 1px);
}

.item-line {
  width: 100%;
  height: var(--item-line-height);
  display: flex;
  align-items: center;
  font-size: .75em;
}

.item-line.end {
  justify-content: flex-end;
}

.item-line.title {
  font-size: 1em;
  line-height: 1em;
  margin-bottom: 2.5px;
  display: flex;
  justify-content: space-between;
}

.item-title-left {
  min-width: 88%;
  height: 100%;
  display: flex;

}

.item-title-text {
  max-width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.item-title-icon {
  min-width: 0;
  max-width: 10%;
}

.item-title-right {
  width: 12%;
  height: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.check-icon {
  width: 20px;
  height: 20px;
  color: #fff;
  opacity: .25;
}

.check-icon-completed {
  width: 20px;
  height: 20px;
  color: rgba(0, 255, 0, 1);
  opacity: 1;
}

.item-bottom {
  width: 100%;
  min-height: calc((var(--item-line-height) * 2 ) - .5px);
  display: flex;
}

.item-bottom-indide {
  height: 100%;
}

.item-bottom-indide.left {
  width: 70%;
}

.item-bottom-indide.right {
  width: 30%;
}

@media screen and (max-width: 767px) {
  .item-div {
    width: calc(100% - 30.5px);
  }

  .item-div.item-selected {
    width: calc(100% - 30.5px);
  }

  .item-div.item-selected {
    width: calc(100% - 30.5px);
  }

  .item-line.date {
    font-size: .65em
  }
}